import { useEffect, useState } from 'react';
import "../css/politica.css"
function Politica() {
  
  /*--------------------------------------------------*/
  return (
    <div className='mainpolitica'>
        <div className="politica">
            <div className='titols'>Normativa de Protecci de Dades (RGPD)</div>
            <div className='textgran'>
                <h3>Introducci</h3>
                <p>Tot seguit us informem de la poltica de privacitat de Xavier Gil Pascual, dacord amb la
                    normativa del Reglament (UE) 2016/679 del Parlament Europeu i del Consell, de 27 dabril de
                    2016, relatiu a la protecci de les persones fsiques pel que fa al tractament de dades
                    personals i a la lliure circulaci daquestes dades i pel qual es deroga la Directiva 95/46/CE
                    (Reglament General de Protecci de Dades, en endavant RGPD).
                </p>
                
                <h3>Entitat responsable</h3>
                <p>La persona responsable de la protecci de dades s qui sencarrega del compliment de la
                    normativa de protecci de dades; entre les seves funcions hi ha la de protegir les dades
                    personals que hgiu proporcionat a Xavier Gil Pascual. Si teniu qualsevol dubte, consulta o
                    petici sobre el tema, li podeu escriure aadas@laplanxisteria.cat.
                    </p>
                <p>La planxisteria est constituda segons la Llei 7/1997, de 18 de juny, dassociacions (DOG 2423,
                    d1 de juliol) i la Llei orgnica 1/2002, de 22 de mar, reguladora del dret dassociaci (BOE
                    nm. 73, de 26 de mar) i els seus estatuts.
                </p>
                <p>Els llocs web i adreces URL derivades dewww.calibraciosistemesadas.cat(en endavant llocs web) sn
                    responsabilitat de Xavier Gil Pascual, i la informaci que shi pugui recaptar sincorporar en
                    els seus sistemes de tractament.
                </p>

                <h3>Persona responsable</h3>
                <p>La persona responsable de la protecci de dades s qui sencarrega del compliment de la
                    normativa de protecci de dades; entre les seves funcions hi ha la de protegir les dades
                    personals que hgiu proporcionat a Xavier Gil Pascual. Si teniu qualsevol dubte, consulta o
                    petici sobre el tema, li podeu escriure aadas@laplanxisteria.cat.
                </p>
                <h3>Limitaci de responsabilitat</h3>
                <p>Xavier Gil Pascual s responsable nicament de la informaci que obt amb prvia sollicitud
                    dels usuaris dels seus llocs web, i no de cap altra informaci personal que de manera eventual
                    aquests poguessin cedir, transmetre o facilitar mitjanant qualsevol altre mecanisme.
                </p>
                <h3>Transparncia</h3>
                <p>Des de la Planxisteria intentem proporcionar als interessats una informaci transparent i
                    concisa, en un llenguatge clar i senzill, pel que fa tant a les condicions del tractament de les
                    seves dades com a les respostes a lexercici dels seus drets.
                </p>
                <h3>Tractament i conservaci de les dades personals</h3>
                <p>Lobjectiu de Xavier Gil Pascual es que el client quedi satisfet en tots els serveis que li posem a
                    labast, per tant, les dades personals que els usuaris proporcionin als llocs web es faran servir
                    exclusivament per fer-los arribar informaci sobre els serveis que ofereix la Planxisteria.
                </p>
                <p>La base que empara els tractaments indicats s lexistncia dun inters legtim: es considera
                    que, com a interessats en els serveis de Xavier Gil Pascual, els usuaris donen el seu
                    consentiment pel que fa a lesmentat tractament de les seves dades personals i a ls de
                    cookies en els llocs web. Aix implica que:
                </p>
                <ul>
                    <li>
                        Les dades seran conservades mentre perduri el consentiment dels usuaris, excepte en
                        aquells casos en qu Xavier Gil Pascual estigui obligada legalment a conservar-les.
                    </li>
                    <li>
                    Dacord amb els drets que els confereix la normativa vigent en protecci de dades, els
                    usuaris podran dirigir-se a lautoritat de control competent per presentar la reclamaci
                    que considerin oportuna, aix com per exercir els drets dAccs, Rectificaci,
                    Cancellaci i Oposici (ARCO) pel que fa al tractament de les seves dades de carcter
                    personal.
                    </li>
                    <li>
                    Quan un usuari comuniqui a Xavier Gil Pascual que vol deixar daparixer en els seus
                    fitxers, seliminaran totes les seves dades que no siguin estrictament necessries per
                    completar les gestions administratives i acadmiques dels cursos de Xavier Gil Pascual
                    a qu shagi inscrit.
                    </li>
                </ul>
                <p>
                    Totes les dades es desen en els servidors de Xavier Gil Pascual o contractats per aquesta, en
                    territori espanyol.
                </p>
                <p>
                    El mer accs als llocs web, aix com a qualsevol dels seus continguts i serveis, implica
                    lacceptaci sense reserves de la present poltica de privacitat, tamb aplicable prviament a la
                    possible contractaci dels serveis que Xavier Gil Pascual posi a disposici dels usuaris. Per tant,
                    s obligatori acceptar i complir aquesta poltica de privacitat abans daccedir als llocs web o
                    contractar un servei de Xavier Gil Pascual.
                </p>
                <p>
                    Lusuari accepta que la navegaci pels llocs web comporta la recollida i tractament de les seves
                    dades en els fitxers de Xavier Gil Pascual. Per tal que lusuari sigui conscient de quina ser la
                    informaci recopilada, com ser tractada i en quines circumstncies es comunicar, Xavier Gil
                    Pascual es compromet a actualitzar en aquesta pgina els canvis que sefectun en la poltica
                    de privacitat, aix com a anunciar-los en qualsevol altre lloc que consideri apropiat.
                </p>
                <h3>Comunicaci a tercers</h3>
                <p>
                    Xavier Gil Pascual no cedir les dades personals de lusuari a tercers, excepte per obligaci
                    legal o requeriment judicial.
                </p>
                <h3>Informaci sobre drets dels interessats i seguretat de la informaci</h3>
                <p>
                    Dacord amb el que estableix per larticle 32 del Reglament (UE) 2016/679 del Parlament
                    Europeu i del Consell, de 27 dabril de 2016, relatiu a la protecci de les persones fsiques pel
                    que fa al tractament de dades personals i a la lliure circulaci daquestes dades i pel qual es
                    deroga la Directiva 95/46/CE (Reglament general de protecci de dades), Xavier Gil Pascual es
                    compromet a adoptar les mesures tcniques i organitzatives necessries per garantir la
                    integritat, confidencialitat i disponibilitat de les dades de lusuari tenint en compte els riscos
                    que acompanyen el seu tractament.
                </p>
                <p>
                    A ms a ms, en cas que lusuari introdueixi dades en els formularis dels llocs web de tercers,
                    seran aquests ltims, i no Xavier Gil Pascual, els responsables de les dades de carcter
                    personal i els que hauran dadoptar les mesures tcniques descrites en el pargraf anterior.
                </p>
                <h3>Drets ARCO</h3>
                <p>
                    Dacord amb el captol 3 del RGPD, totes les persones poden exercir els drets dAccs,
                    Rectificaci, Cancellaci i Oposici (ARCO) amb relaci a les seves dades personals, de manera
                    que totes les entitats estan obligades a garantir-ne el compliment. Aix, respecte a Xavier Gil
                    Pascual, a travs del canal datencixavigil@laplanxisteria.catlusuari pot exercir qualsevol dels
                    drets segents (cliqueu-hi a sobre per descarregar-vos el model de sollicitud):
                </p>
                <ul>
                    <li>
                        Dret dAccs: podeu consultar les vostres dades personals incloses en els nostres
                        fitxers.
                    </li>
                    <li>
                        Dret de Rectificaci: podeu demanar-nos que modifiquem les vostres dades personals
                        si sn inexactes.
                    </li>
                    <li>
                        Dret de Cancellaci o supressi: podeu demanar-nos que eliminem les vostres dades
                        personals dels nostres fitxers.
                    </li>
                    <li>
                        Dret dOposici: podeu oposar-vos al tractament de les vostres dades personals per
                        part de Xavier Gil Pascual, sempre que hi hagi un motiu legtim i fonamentat per fer-
                        ho.
                    </li>
                </ul>
                <p>
                    Lusuari garanteix que les dades proporcionades sn veritables, exactes, completes i
                    actualitzades i s responsable de qualsevol dany o perjudici, directe o indirecte, que pogus
                    ocasionar-se com a conseqncia de lincompliment daquesta obligaci tant a Xavier Gil
                    Pascual com a tercers.
                </p>
                <h3>Consentiment per al compliment de la LSSI-CE</h3>
                <p>
                    En compliment de la Llei 34/2002, d11 de juliol, de serveis de la societat de la informaci i de
                    comer electrnic (LSSI-CE), Xavier Gil Pascual sollicitar a lusuari el seu consentiment exprs
                    (i aquest, si aix ho desitja, lhi prestar) per poder fer s de les seves dades amb la finalitat
                    denviar-li informaci sobre el lloc web i diferents ofertes i promocions relacionades.
                </p>
                <p>
                    Dins cada comunicaci comercial de Xavier Gil Pascual es facilitar a lusuari un enlla que li
                    permetr revocar en qualsevol moment el seu consentiment a rebre-les.
                </p>
                <p>
                    Amb lobjectiu de garantir la seguretat i confidencialitat de les dades personals de lusuari,
                    Xavier Gil Pascual ha adoptat els nivells de seguretat requerits per la normativa en protecci
                    de dades personals i ha installat els mitjans tcnics al seu abast per evitar la prdua, mal s,
                    alteraci, accs no autoritzat i robatori de les dades personals facilitades.
                </p>
            </div>
        </div>

    </div>
)
}

export default Politica;
