import { useEffect, useState } from 'react';
import "../css/tipus.css"
import * as React from 'react';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import isa from "../img/tipus/isa.jpg"
import rev from "../img/tipus/rev.jpg"
import bsm from "../img/tipus/bsm.jpg"
import fcw from "../img/tipus/fcw.jpg"
import ldw from "../img/tipus/ldw.png"
import aeb from "../img/tipus/aeb.jpg"
import tsr from "../img/tipus/tsr.png"
import rcta from "../img/tipus/rcta.jpg"
import ddr from "../img/tipus/ddr.jpeg"
import lka from "../img/tipus/lka.jpg"
import aebpc from "../img/tipus/aebpc.png"
import ess from "../img/tipus/ess.jpg"
function Tipus() {
    const [ampleralla3, setAmpleralla3] = useState("0%")
    const [primeravegada3, setPrimeravegada3] = useState(true)
    const [path, setPath] = useState(window.location.pathname)

    useEffect (() => {
        if (path === "/tipus"){
            setAmpleralla3("100%")
        }
        else{
            setAmpleralla3("0%")

        }
    })
    const Item = styled(Paper)(({ theme }) => ({

    }));
  return (
    <div className="primer2">
        <div style={{display:"flex",flexDirection:"column", marginBottom:"4%", marginTop:"2%", textAlign:"justify", width:"80%"}}>
          <h1>TIPUS DE SISTEMES ADAS</h1>
          <div style={{height:"2px", width:ampleralla3, backgroundColor:"black", transition:"all 1s ease-in-out"}}></div>
        </div>
            <div className="tipus">
                <Grid container spacing={2}>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                        <div className="fons">
                            <div>
                                <img src={isa} className='fotoadas'></img>
                            </div>
                            <div>
                                <div className="titolstipus">
                                    ISA (Asistent intelligent de velocitat)
                                </div>
                                <div className="subtitolstipus">
                                    Ajuda els conductors a mantenir-se dins dels lmits de velocitat mitjanant avisos i intervencions automtiques, utilitzant GPS i reconeixement de senyals de trnsit.
                                </div>
                            </div>
  
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={rev} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        REV (Detector de marxa enrere)
                                    </div>
                                    <div className="subtitolstipus">
                                        Alerta al conductor sobre obstacles darrere del vehicle mitjanant sensors i cmeres, millorant la seguretat en maniobres de reversa.
                                    </div>
                                </div>
                            </div>
                        
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                        <div className="fons">
                            <div>
                                <img src={bsm} className='fotoadas'></img>
                            </div>
                            <div>
                                <div className="titolstipus">
                                    BSM (Sistema de monitoritzaci d'angles morts)
                                </div>
                                <div className="subtitolstipus">
                                    Detecta vehicles en els angles morts del conductor, emetent avisos visuals o sonors per evitar collisions durant els canvis de carril.                        </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                        <div className="fons">
                            <div>
                                <img src={fcw} className='fotoadas'></img>
                            </div>
                            <div>
                                <div className="titolstipus">
                                    FCW (Avs de Collisi Frontal)
                                </div>
                                <div className="subtitolstipus">
                                    Alerta al conductor sobre possibles collisions frontals, incloent-hi vianants i ciclistes, per prevenir accidents.
                                    </div>
                            </div>
                        </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={ldw} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        LDW (Sistema d'Advertncia d'Abandonament de Carril)
                                    </div>
                                    <div className="subtitolstipus">
                                        Alerta al conductor quan el vehicle es desvia del seu carril sense senyalitzar, ajudant a prevenir sortides involuntries de la via.
                                    </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                        <div className="fons">
                            <div>
                                <img src={aeb} className='fotoadas'></img>
                            </div>
                            <div>
                                <div className="titolstipus">
                                    AEB (Frenada d'Emergncia Urbana i Interurbana)
                                </div>
                                <div className="subtitolstipus">
                                    Activa els frens automticament per evitar collisions o reduir la seva gravetat tant en entorns urbans com interurbans.                        </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={tsr} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        TSR (Reconeixement de Senyals de Trnsit)
                                    </div>
                                    <div className="subtitolstipus">
                                        Identifica i mostra les senyals de trnsit al conductor, ajudant-lo a seguir les normes viries.
                                    </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={rcta} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        RCTA (Alerta de Trnsit Creuat)
                                    </div>
                                    <div className="subtitolstipus">
                                        Avisa al conductor sobre vehicles que s'aproximen pels costats durant la marxa enrere, millorant la seguretat en maniobres de sortida.
                                    </div>
                                </div>
                            </div> 
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={ddr} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        DDR (Sistema d'Advertncia de Sonolncia i Distracci)
                                    </div>
                                    <div className="subtitolstipus">
                                        Detecta signes de fatiga o distracci en el conductor i emet avisos per alertar-lo, ajudant a mantenir l'atenci al volant.                        </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={lka} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        LKA (Sistema Avanat de Manteniment de Carril)
                                    </div>
                                    <div className="subtitolstipus">
                                        Ajuda a mantenir el vehicle dins del seu carril mitjanant correccions automtiques de la direcci si es detecta una desviaci involuntria.
                                    </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4}>
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={aebpc} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        AEB + P + C (Sistema de Frenada Autnoma d'Emergncia + detecci de peatons i ciclistes)
                                    </div>
                                    <div className="subtitolstipus">
                                        Aplica els frens automticament per evitar collisions amb vianants i ciclistes, millorant la seguretat en situacions d'emergncia.                        </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                    <Grid item xs={12} md={6} xl={4} >
                        <Item>
                            <div className="fons">
                                <div>
                                    <img src={ess} className='fotoadas'></img>
                                </div>
                                <div>
                                    <div className="titolstipus">
                                        ESS (Sistema de Frenada d'Emergncia)
                                    </div>
                                    <div className="subtitolstipus">
                                        Activa automticament els frens per reduir la velocitat o aturar el vehicle en situacions d'urgncia per evitar collisions.
                                    </div>
                                </div>
                            </div>
                        </Item>
                    </Grid>
                </Grid>
            </div>
            <div style={{marginTop:"3%"}}>
                <div style={{width:"100%", height:"1px", backgroundColor:"black"}}></div>
                <span className='titols'>POTS CONSULTAR MS INFORMACI A LA PGINA DE LA DGT</span>
                <div className='link'><a target=' ' href='https://www.dgt.es/muevete-con-seguridad/conviertete-en-un-buen-conductor/Sistemas-avanzados-de-ayuda-a-la-conduccion-ADAS-/'><div className='boto3'>DGT</div></a></div>
                <div style={{width:"100%", height:"1px", backgroundColor:"black", marginBottom:"5%"}}></div>

            </div>
    </div>
)
}

export default Tipus;
